// Helper function for static asset paths
// Currently configured to NOT add any prefix - assets load from root

export const assetPath = (path: string): string => {
  // If path already starts with http or //, return as is
  if (path.startsWith('http') || path.startsWith('//')) {
    return path
  }
  
  // Add leading slash if not present and return without prefix
  return path.startsWith('/') ? path : `/${path}`
}

